<template>
  <div class="dashboard">
    <h1 class="subheading grey--text ml-4">Dashboard</h1>
    
    <v-container class="my-4">

      <v-layout row class="mb-3" wrap>

        <v-tooltip top>
          <v-btn small flat color="grey" @click="sortBy('title')" slot="activator">
            <v-icon left small>folder</v-icon>
            <span class="caption text-lowercase">By project name</span>
          </v-btn>
          <span>Sort projetcs by project name</span>
        </v-tooltip>

        <v-tooltip top>
          <v-btn small flat color="grey" @click="sortBy('status')" slot="activator">
            <v-icon left small>check_circle</v-icon>
            <span class="caption text-lowercase">By status</span>
          </v-btn>
           <span>Sort projects by status</span>
        </v-tooltip>

      </v-layout>
      
      <v-card flat v-for="(project, index) in projects" :key="project.title">
        <v-layout row wrap :class="`pa-2 project ${project.status}`">

          <v-flex xs12 md6>
            <div class="caption grey--text">Project Title</div>
            <div>{{ project.title }}</div>
          </v-flex>

          <v-flex xs6 sm4 md2>
            <div class="caption grey--text">Due by</div>
            <div>{{ project.due }}</div>
          </v-flex>

          <v-flex xs2 sm4 md2>
            <div class="caption grey--text">Status</div>
            <div>
              <v-btn @click="changeStatus(index)" flat small round :class="`${project.status} white--text my-2 `">
                {{ project.status }}
              </v-btn>
            </div>
          </v-flex>
          <v-spacer></v-spacer>

          <v-flex xs2 sm4 md2>
            <div class="caption grey--text">Actions</div>
            <div><v-btn icon :class="`${project.status}`" @click="deleteProject(index)"><v-icon>delete</v-icon></v-btn></div>
          </v-flex>

        </v-layout>
        <v-divider></v-divider>
      </v-card>
      
    </v-container>


  </div>
</template>

<script>
  import db from '@/fb'
  import firebase from 'firebase'


  export default {
    data() {
      return {
        projects: [],
        userId: '',
        btnColor: ''
      }
    },

    methods: {

      sortBy(prop){
        this.projects.sort((a,b) => a[prop] < b[prop] ? -1 : 1)
      },

      changeStatus(index) {
      
        const currentProject = this.projects[index]
        var docRef = db.collection("users/"+this.userId+"/projects").doc(currentProject.id);

        // LOGIC FOR STATUS UPDATE
        switch (currentProject.status) {
          case 'ongoing':
            var newStatus = 'complete'
            var btnColor = 'success'
            break;

          case 'complete':
            newStatus = 'overdue'
            btnColor = 'error'
            break;
          
          case 'overdue':
            newStatus = 'ongoing'
            btnColor = 'info'
            break;

          default:
            break;
        }

        // UPDATE DATABASE
        var setWithMerge = docRef.set({
            status: newStatus,

        }, { merge: true });

        // UPDATE LOCAL DATA
        currentProject.status = newStatus
        this.btnColor = btnColor

      },

      deleteProject(index) {
        const currentProject = this.projects[index]
        var docRef = db.collection("users/"+this.userId+"/projects").doc(currentProject.id);

        // DELETE ON DATABASE
        docRef.delete().then(function() {
          console.log("Document successfully deleted!");
        }).catch(function(error) {
          console.error("Error removing document: ", error);
        });


        // DELETE ON LOCAL DATA
        if (index > -1) {
          this.projects.splice(index, 1);
        }

      },


    },

    created() {
      const user = firebase.auth().currentUser;
      if (user) {
        var userId = user.uid
        this.userId = userId
      } else {
        // not logged
      }

      // GET DATA
      db.collection('users/'+userId+'/projects').onSnapshot(res => {
 
        const changes = res.docChanges();

        changes.forEach(change => {
          if (change.type === 'added') {
            this.projects.push({
              ...change.doc.data(),
              id: change.doc.id
            })
          }
        })
      })
    }
  }

</script>

<style>

  .project.complete {
    border-left: 4px solid #3cd1c2;
  }

  .project.ongoing {
    border-left: 4px solid #ffaa2c;
  }

  .project.overdue {
    border-left: 4px solid #f83e70;
  }

  .theme--light.v-btn:not(.v-btn--icon).complete {
    background-color: #3cd1c2;
  }

  .theme--light.v-btn:not(.v-btn--icon).ongoing {
    background-color: #ffaa2c;
  }

  .theme--light.v-btn:not(.v-btn--icon).overdue {
    background-color: #f83e70;
  }

  .theme--light.v-btn.complete {
    color: #3cd1c2;
  }

  .theme--light.v-btn.ongoing {
    color: #ffaa2c;
  }

  .theme--light.v-btn.overdue {
    color: #f83e70;
  }

</style>
